import { generateRSAKeys, arrayBufferToPem, pemToArrayBuffer, aesEncrypt, aesDecrypt } from './crypto';

export class WebSocketClient {
    private url: string;
    private socket: WebSocket | null = null;
    private isConnected: boolean = false;
    private rsaPublic: CryptoKey | null = null;
    private rsaPrivate: CryptoKey | null = null;
    private rsaPublicServer: CryptoKey | null = null;
    private aesKey: CryptoKey | null = null;
    private aesServerKey: CryptoKey | null = null;
    private messageHandlers: Map<string, (data: any) => void> = new Map();
    private connectionPromise: Promise<void> | null = null;

    constructor(url: string) {
        this.url = url;
    }

    public async connect(): Promise<void> {
        if (this.connectionPromise) {
            return this.connectionPromise;
        }

        this.connectionPromise = new Promise((resolve, reject) => {
            try {
                this.socket = new WebSocket(this.url);

                this.socket.onopen = async () => {
                    console.log('WebSocket connected');
                    // Генерируем RSA ключи
                    const keyPair = await generateRSAKeys();
                    this.rsaPublic = keyPair.publicKey;
                    this.rsaPrivate = keyPair.privateKey;

                    // Отправляем публичный ключ
                    const publicKeyPem = await arrayBufferToPem(this.rsaPublic, "PUBLIC KEY");
                    this.socket?.send(JSON.stringify({
                        type: "key_exchange",
                        key: publicKeyPem
                    }));
                };

                this.socket.onmessage = async (event) => {
                    try {
                        const data = JSON.parse(event.data);
                        console.log('WebSocket message:', data);

                        if (data.type === "key_exchange") {
                            if (data.key && !this.rsaPublicServer) {
                                // Получаем публичный ключ сервера
                                this.rsaPublicServer = await pemToArrayBuffer(data.key);
                                this.isConnected = true;
                                resolve();
                            }
                        } else {
                            // Расшифровываем сообщение, если есть ключи
                            if (this.aesServerKey) {
                                const decrypted = await aesDecrypt(new Uint8Array(data), this.aesServerKey);
                                const message = JSON.parse(decrypted);
                                
                                // Вызываем соответствующий обработчик
                                const handler = this.messageHandlers.get(`${message.type}:${message.action}`);
                                if (handler) {
                                    handler(message);
                                }
                            }
                        }
                    } catch (error) {
                        console.error('Error processing message:', error);
                    }
                };

                this.socket.onerror = (error) => {
                    console.error('WebSocket error:', error);
                    reject(error);
                };

                this.socket.onclose = () => {
                    console.log('WebSocket closed');
                    this.isConnected = false;
                    this.connectionPromise = null;
                };
            } catch (error) {
                reject(error);
            }
        });

        return this.connectionPromise;
    }

    public async send(message: any): Promise<void> {
        if (!this.isConnected || !this.socket || !this.aesServerKey) {
            throw new Error('WebSocket not connected or keys not exchanged');
        }

        const encrypted = await aesEncrypt(JSON.stringify(message), this.aesServerKey);
        this.socket.send(encrypted);
    }

    public on(type: string, action: string, handler: (data: any) => void): void {
        this.messageHandlers.set(`${type}:${action}`, handler);
    }

    public off(type: string, action: string): void {
        this.messageHandlers.delete(`${type}:${action}`);
    }

    public close(): void {
        if (this.socket) {
            this.socket.close();
            this.socket = null;
            this.isConnected = false;
            this.connectionPromise = null;
        }
    }
} 